body {
  overflow-y: hidden;
}

@media screen and (max-width: 540px) {
  .short-video-container {
    width: 100%;
    margin: 0;
    height: 100vh;
  }
}

.slick-arrow {
  display: none !important;
}

.slick-slider {
  width: 100%;
  height: 100%;
}

.slick-list,
.slick-track,
.slick-slide {
  width: 100%;
  height: 100% !important;
}

.slick-track > div {
  box-sizing: content-box; /* So the width will be 100% + 17px */
  height: 100%;
  width: 100%;
